.head {
  position: relative;
  margin-top: 76px;
  overflow: hidden;
  height: 600px;
}
.head .bgimg {
  position: absolute;
  width: 5000px;
  height: 600px;
  top: 0;
  left: 50%;
  margin-left: -2500px;
  background-color: #fafafa;
}
.head .bgimg img {
  position: relative;
  display: block;
  height: 130%;
  margin: 0 auto;
  top: -15%;
}
.head .blkcent {
  box-sizing: border-box;
  padding-left: 110px;
}
.head .blkcent h1 {
  font-size: 48px;
  width: 550px;
  font-weight: bold;
  color: #4d4d4d;
  line-height: 61px;
  margin-top: 120px;
}
.head .blkcent h2 {
  font-size: 29px;
  font-weight: 400;
  color: #4a4a4a;
  line-height: 41px;
  margin-top: 25px;
}
.head .blkcent p {
  width: 550px;
  font-size: 15px;
  color: #4a4a4a;
  line-height: 25px;
  margin-top: 15px;
}
.head .blkcent button {
  width: 224px;
  height: 60px;
  background: linear-gradient(225deg, #fa2b56 0%, #f91c3d 100%);
  box-shadow: 20px 20px 60px 0px rgba(0, 0, 0, 0.4);
  border-radius: 5px;
  outline: none;
  border: none;
  margin-top: 60px;
  font-size: 22px;
  color: #ffffff;
  transition-duration: 0.3s;
  cursor: pointer;
}
.head .blkcent button:hover {
  opacity: 0.8;
}
.apps {
  margin-top: 80px;
  margin-bottom: 130px;
}
.apps .left,
.apps .right {
  position: relative;
  width: 50%;
  padding: 20px;
  box-sizing: border-box;
  height: 410px;
  float: left;
}
.apps .bg {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.apps .applogo {
  position: relative;
  margin: 40px 0 0 30px;
  width: 100px;
  z-index: 10;
}
.apps p {
  position: relative;
  font-size: 14px;
  color: #ffffff;
  width: 210px;
  margin-left: 40px;
  line-height: 25px;
  z-index: 10;
}
.apps .donwload {
  position: absolute;
  left: 30px;
  bottom: 50px;
  margin-left: 30px;
}
.apps .donwload img {
  width: 110px;
  margin-right: 20px;
}
.product .grid3 {
  margin-top: 70px;
}
.product .griditem {
  width: 33%;
  padding: 0 50px;
  text-align: center;
  box-sizing: border-box;
  float: left;
}
.product .griditem .appicon {
  width: 66px;
  margin: 0 auto;
  display: block;
}
.product .griditem h4 {
  font-size: 22px;
  font-weight: 400;
  color: #4A4A4A;
  padding: 15px;
}
.product .griditem p {
  font-size: 15px;
  color: #666666;
  line-height: 26px;
  height: 90px;
}
.product .griditem a {
  font-size: 18px;
  color: #496DEC;
  padding: 18px;
}
.product .griditem .apppage {
  width: 260px;
  display: block;
  margin: 0 auto;
  margin-top: 30px;
}
.tob {
  margin: 150px 0;
}
.tob .dubtitle {
  margin-bottom: 70px;
}
.tob .left,
.tob .right {
  position: relative;
  float: left;
  width: 46%;
  margin: 0 2%;
  padding: 30px;
  box-sizing: border-box;
  border-radius: 4px;
  box-shadow: 0px 2px 26px 0px rgba(0, 0, 0, 0.1);
  background-color: #fff;
}
.tob .left img,
.tob .right img {
  width: 80px;
  position: absolute;
  top: 30px;
  left: 30px;
  border-radius: 50%;
}
.tob .left h4,
.tob .right h4 {
  font-size: 22px;
  color: #4A4A4A;
  margin-left: 120px;
  font-weight: 400;
}
.tob .left p,
.tob .right p {
  margin-left: 120px;
  line-height: 26px;
  color: #666666;
  margin-top: 10px;
}
.tob .left div,
.tob .right div {
  text-align: right;
  margin-top: 20px;
}
.tob .left a,
.tob .right a {
  text-align: right;
  color: #496DEC;
  font-size: 18px;
}
